<template>
    <div>
        <!-- Button按钮 -->
        <el-row id="button">
            <el-button icon="el-icon-search" circle></el-button>
            <el-button type="primary" icon="el-icon-edit" circle></el-button>
            <el-button type="success" icon="el-icon-check" circle></el-button>
            <el-button type="info" icon="el-icon-message" circle></el-button>
            <el-button type="warning" icon="el-icon-star-off" circle></el-button>
            <el-button type="danger" icon="el-icon-delete" circle></el-button>
        </el-row>

        <!-- pagination分页组件 -->
        <el-pagination id="page" background layout="prev, pager, next, sizes" @size-change="handleSizeChange"
            @current-change="handleCurrentChange" :total="1000">
        </el-pagination>

        <!-- 嵌套表单的dialog -->
        <el-button type="text" @click="dialogFormVisible = true">Login</el-button>

        <el-dialog title="密码登录" :visible.sync="dialogFormVisible" width="40%" center>
            <el-form :model="form">
                <el-form-item label="账号" :label-width="formLabelWidth">
                    <el-input v-model="form.name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="密码" :label-width="formLabelWidth">
                    <el-input placeholder="请输入密码" v-model="form.passwoed" show-password></el-input>
                </el-form-item>
            </el-form>
            <!-- 是否同意协议 -->
            <el-checkbox id="agree" v-model="checked">我已阅读并同意 服务条款 和 隐私协议</el-checkbox>
            <div style="text-align: center;" slot="footer" class="dialog-footer">
                <el-button type="primary" @click="onSubmit">登 录</el-button>
            </div>
        </el-dialog>

    </div>

</template>

<script>
export default {
    data() {
        return {
            dialogFormVisible: false,
            form: {
                name: '',
                passwoed: ''
            },
            formLabelWidth: '40px',
            checked: 'false',

        }
    },
    methods: {
        onSubmit() {
            console.log(JSON.stringify(this.form));
        },
        handleSizeChange(val) {
            alert("每页记录数变化" + val)
        },
        handleCurrentChange(val) {
            alert("页码发生变化" + val)
        }
    }
}

</script>

<style>
#button {
    text-align: center;
}

#page {
    text-align: center;
}
#agree {
    text-align: left;
}
</style>